@import '../../../styles/variables'

.header
  display: flex
  padding-bottom: 3rem

.header img
  width: 30%
  border-radius: 0.4rem
  min-height: 10rem
  min-width: 10rem

.marks
  padding: 0 0 0 2rem

.info
  border-top: $border
  padding: 3rem
  display: flex
  justify-content: space-around

.ingredients
  text-align: center
  width: 30%

.instructions
  text-align: center
  width: 50%

.mb
  margin-bottom: 2rem

.container
  padding: 3rem
  margin: 0 auto
  text-align: center
  height: 80vh
  border-top: $border

.container iframe
  border: none
  width: 100%
  height: 100%

@media (max-width: $width-lg)
  .container
    height: 50vh

@media (max-width: $width-md)
  .container
    height: 40vh

@media (max-width: $width-sm)
  .container
    padding: 2rem
    height: 30vh

@media (max-width: $width-xs)
  .header
    display: block
    text-align: center

  .header img
    margin-bottom: 2rem
    width: 60%

  .marks
    padding: 0

  .info
    display: block
    padding: 2rem

  .ingredients
    width: 100%
    margin-bottom: 2rem

  .instructions
    width: 100%
